import React from 'react'

import { Typography } from '@material-ui/core'
import withStyles from '@material-ui/core/styles/withStyles'
import UploadIcon from '@material-ui/icons/CloudDownload'
import Dropzone from 'react-dropzone'

const CustomDropZone = ({ acceptedMimeTypes, onChange, classes, disabled }) => {
  const [fileName, setFileName] = React.useState(null)

  const onFileDrop = (files, rejectedFiles) => {
    if (rejectedFiles.length > 0) return

    const [newFile] = files;

    onChange(newFile);
    setFileName(newFile.name);
  }

  const dropzoneClassName = ({ isDragActive, isDragReject, disabled }) => {
    if (disabled) {
      return classes.Dropzone_disabled
    }
    if (isDragReject) {
      return classes.Dropzone_onDragReject
    }
    if (isDragActive) {
      return classes.Dropzone_onDrag
    }

    return null
  }

  return (
    <Dropzone
      onDrop={onFileDrop}
      accept={acceptedMimeTypes}
      multiple={false}
    >
      {({ getRootProps, getInputProps, isDragActive, isDragReject }) => {
        const className = dropzoneClassName({ isDragActive, isDragReject, disabled });

        return (
          <div
            variant='contained'
            color='primary'
            {...getRootProps()}
            className={`${classes.Dropzone} ${className}`}
          >
            <input {...getInputProps()} />
            <UploadIcon />
            <div>
              {
                !isDragReject
                  ? 'Arraste o arquivo aqui, ou clique para adicionar'
                  : 'Formato não suportado'
              }
            </div>
            {
              fileName &&
              <div className={classes.Dropzone_selectedFile}>
                <Typography>
                  Selecionado:
                </Typography>
                <Typography variant='body1'>
                  {` ${fileName}`}
                </Typography>
              </div>
            }
          </div>
        )
      }}
    </Dropzone>
  )
}

const styles = theme => ({
  Dropzone: {
    margin: '5px 0',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    color: '#101010',
    backgroundColor: '#F5F5F5',
    padding: '10px 16px',
    fontSize: '0.875rem',
    minWidth: '64px',
    borderRadius: '4px',
    fontWeight: 500,
    fontFamily: 'Roboto, Helvetica, Arial, sans-serif',
    border: '2px solid transparent',
    cursor: 'pointer',
    outline: 'none',
    '&:hover': {
      backgroundColor: '#D9D9D9',
    }
  },
  Dropzone_onDrag: {
    backgroundColor: '#FAFAFA',
    color: '#497530',
    border: '2px dashed #497530',
  },
  Dropzone_onDragReject: {
    backgroundColor: '#FAFAFA',
    color: '#D32F2F',
    border: '2px dashed #D32F2F',
  },
  Dropzone_disabled: {
    opacity: 0.6,
    '&:hover': {
      backgroundColor: 'inherit',
      cursor: 'not-allowed'
    }
  },
  Dropzone_selectedFile: {
    color: 'inherit',
    display: 'flex',
    marginTop: theme.spacing(),
    textTransform: 'none'
  },
})

CustomDropZone.defaultProps = {
  onChange: () => { },
  acceptedMimeTypes: [
    'image/*'
  ],
  label: 'Arquivo',
  currentFileName: '',
  disabled: false
}
export default withStyles(styles)(CustomDropZone)
