import React, { useEffect, useRef } from 'react';

import SamplesTable from './components/SamplesTable';
import Calculate from './components/Calculate';
import BatchesUploadModal from './components/BatchesUploadModal';
import setLoading from '../../utils/setLoading';

import { Button, Box } from '@material-ui/core';
import { generateNewSample } from '../../models/sample';
import getRandomSampleData from '../../utils/getRandomSampleData';
import calculateData from '../../services/calculateData';

const Samples = () => {
  const [samples, setSamples] = React.useState([]);
  const [calculationResult, setCalculationResult] = React.useState(null);
  const [modalIsOpen, setModalOpen] = React.useState(false);
  const calculationRef = useRef(null);

  useEffect(() => {
    setLoading(false)
  }, [samples, calculationResult])

  const handleCalculation = () => {
    setLoading(true);
    const result = calculateData(samples);

    setCalculationResult(result);

    window.scrollTo(0, calculationRef.current.offsetTop)
  }

  const generateRandomSamples = () => {
    setSamples(getRandomSampleData());
  }

  const handleSampleAdd = (sample) => {
    const newSample = generateNewSample(sample)

    setSamples([...samples, newSample])
  }

  const handleSampleUpdate = (updatedSample) => {
    const updatedSamples = samples.map(sample =>
      sample.id === updatedSample.id
        ? {
          ...sample,
          ...updatedSample
        }
        : sample
    )

    setSamples(updatedSamples)
  }

  const handleDeleteSample = (deletedSample) => {
    const updatedSamples = samples.filter(sample =>
      sample.id !== deletedSample.id
    )

    setSamples(updatedSamples)
  }

  const handleBatchUploadSamples = (data) => {
    setLoading(true);
    setModalOpen(false);

    const newSamples = data.map(generateNewSample);

    setSamples(newSamples);
  }

  return (
    <>
      <Box justifyContent="flex-end" marginBottom={2} display="flex">
        <Button onClick={generateRandomSamples} variant="outlined" color="primary">
          Gerar Amostras Aleatórias
        </Button>

        <Box marginLeft={2}>
          <Button variant="contained" color="primary" onClick={() => setModalOpen(true)}>
            Adicionar Amostras por Arquivo
          </Button>
        </Box>
      </Box>

      <SamplesTable
        samples={samples}
        onSampleAdd={handleSampleAdd}
        onSampleDelete={handleDeleteSample}
        onSampleUpdate={handleSampleUpdate}
      />

      <span ref={calculationRef}>
        <Calculate
          calculationResult={calculationResult}
          disabled={samples.length === 0}
          handleCalculation={handleCalculation}
        />
      </span>

      <BatchesUploadModal
        open={modalIsOpen}
        onClose={() => setModalOpen(false)}
        onSubmit={handleBatchUploadSamples}
      />
    </>
  )
}

export default Samples;
