import React from 'react';
import {
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  DialogContentText,
} from '@material-ui/core';

import { Alert } from '@material-ui/lab'

import Dropzone from '../../../../components/Dropzone';
import LoadingWrapper from '../../../../components/LoadingWrapper';

import { parseFile, acceptedMimeTypes, acceptedFormats } from '../../../../models/batchUpload';

const BatchesUploadModal = ({ open, onClose, onSubmit }) => {
  const [error, setError] = React.useState(null);
  const [data, setData] = React.useState([]);
  const [loading, setLoading] = React.useState(false)

  const handleFileDrop = async (file) => {
    setLoading(true);

    try {
      const parsedData = await parseFile(file);

      setData(parsedData);
      setError(null);
    }
    catch ({ message }) {
      setError(message)
    }

    setLoading(false);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    onSubmit(data);
  }

  return (
    <Dialog
      fullWidth
      open={open}
      onClose={onClose}
      disableBackdropClick
    >
      <LoadingWrapper loading={loading}>
        <DialogTitle>
          <Box display="flex" justifyContent="space-between">
            Adicionar Amostras
            <Button
              color='primary'
              variant='outlined'
              onClick={() => window.open('/s-dimorph/tabela-amostras-exemplo.csv')}
            >
              Baixar Modelo
            </Button>
          </Box>
        </DialogTitle>


        <DialogContent>
          <DialogContentText>
            Formatos aceitos: {acceptedFormats.join(', ')}
          </DialogContentText>
          <Dropzone
            onChange={handleFileDrop}
            acceptedMimeTypes={acceptedMimeTypes}
          />

          {!!error &&
            <Box marginTop="16px">
              <Alert severity='error'>
                Erro! {error}
              </Alert>
            </Box>
          }
        </DialogContent>


        <DialogActions>
          <Button
            color='primary'
            onClick={onClose}
          >
            Cancelar
          </Button>

          <Button
            color='primary'
            variant='contained'
            onClick={handleSubmit}
            disabled={!!error || !data.length}
          >
            Confirmar
          </Button>
        </DialogActions>
      </LoadingWrapper>
    </Dialog>
  )
}

export default BatchesUploadModal;
