import XLSX from 'xlsx';

const parse = (file) =>
  new Promise((resolve, reject) => {
    const fileReader = new FileReader();

    fileReader.onload = ({ target: { result: data } }) => {
      const readedSheets = XLSX.read(data, {
        type: 'binary'
      });

      const [sheetName] = readedSheets.SheetNames;
      const sheet = readedSheets.Sheets[sheetName];

      const dataParsed = XLSX.utils.sheet_to_row_object_array(sheet, { defval: null })

      resolve(dataParsed)
    };

    fileReader.onerror = (error) => {
      reject(error);
    };

    fileReader.readAsBinaryString(file);
  })

export default { parse }
