import Parser from '../services/parseSheet';
import { sexValues } from './sample';
import isEmpty from '../utils/isEmpty';

const throwError = (message) => {
  throw new Error(message);
}

const COLUMNS = {
  speciesName: {
    validate: (value, row) => {
      if (isEmpty(value)) {
        throwError(`Linha ${row}: O nome da espécie não pode ser vazio`);
      }

      return value;
    }
  },
  sex: {
    validate: (value, row) => {
      if (isEmpty(value)) {
        throwError(`Linha ${row}: O sexo não pode ser vazio`);
      }

      const sexValuesKeys = Object.keys(sexValues);
      const upperValue = value.toUpperCase();

      if (!sexValuesKeys.includes(upperValue)) {
        throwError(`Linha ${row}: O sexo deve ser um dos valores: ${sexValuesKeys.toString()}`);
      }

      return upperValue;
    }
  },
  size: {
    validate: (value, row) => {
      if (isEmpty(value)) {
        throwError(`Linha ${row}: O tamanho não pode ser vazio`);
      }

      const number = Number(value);

      if (isNaN(number)) {
        throwError(`Linha ${row}: Tamanho deve ser um número`);
      }

      return number;
    }
  }
}

export const FORMATS = [
  { format: 'csv', mimeType: 'text/csv' },// .csv
  { format: 'ods', mimeType: 'application/vnd.oasis.opendocument.spreadsheet' }, // .ods
  { format: 'xls', mimeType: 'application/vnd.ms-excel' }, //.xls Microsoft Excel
  { format: 'xlsx', mimeType: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }, //.xlsx Microsoft Excel (OpenXML)
]

export const acceptedFormats = FORMATS.map(({ format }) => format);

export const acceptedMimeTypes = FORMATS.map(({ mimeType }) => mimeType);

const validateData = (data) => {
  if (!data.length) throwError('A tabela não possui nenhum registro.');

  return data.map((line, index) =>
    Object.keys(line).reduce((element, lineKey) => {
      const elementColumn = COLUMNS[lineKey];
      const currentValue = line[lineKey];
      const row = index + 2;

      element[lineKey] = elementColumn.validate
        ? elementColumn.validate(currentValue, row)
        : currentValue;

      return element
    }, {})
  )
}

const deserializeData = (data) => data.map(element => {
  const dataKeys = Object.keys(COLUMNS);

  return Object.keys(element).reduce((object, key, index) => {
    object[dataKeys[index]] = element[key];

    return object
  }, {})
})

export const parseFile = async (file) => {
  const parsedData = await Parser.parse(file);

  const deserializedData = deserializeData(parsedData);

  return validateData(deserializedData)
}
