export const calculationResultTableHeaders = [
  {
    title: "Espécie",
    field: 'species',
  },
  {
    title: "Numero de amostras",
    field: 'samples',
  },
  {
    title: "Média tamanho fêmeas",
    field: 'femalesAverage',
  },
  {
    title: "Média tamanho machos",
    field: 'malesAverage',
  },
  {
    title: "SDI",
    field: 'SDI',
  }
]

export const deserializeTableValues = data =>
  data.map(row => ({
    ...row,
    samples: row.samples.length,
    malesAverage: row.malesAverage.toFixed(2),
    femalesAverage: row.femalesAverage.toFixed(2),
    SDI: row.SDI.toFixed(2),
  }))
